import { PubSubService } from '@ohif/core';
import { Types } from '@cornerstonejs/core';
import { PanoramicState } from '../../types/Panoramic';
import { annotation as cstAnnotation } from '@cornerstonejs/tools';
import { initial } from 'lodash';
import { panoramicViewport } from '../../hps/panoramicViewport';

export default class PanoramicService extends PubSubService {
  private enabled: boolean = false;
  private panoramicStates: { [displaySetUID: string]: PanoramicState } = {};

  static EVENTS = {
    PANORAMIC_ACTIVATED: 'event::PanoramicService:panoramicActivated',
    PANORAMIC_STATE_UPDATED: 'event::PanoramicService:panoramicStateUpdated',
  };

  public static REGISTRATION = {
    name: 'panoramicService',
    create: () => {
      return new PanoramicService();
    },
  };

  constructor() {
    super(PanoramicService.EVENTS);
  }

  public activatePanoramic = () => {
    this.enabled = true;
    this._broadcastEvent(this.EVENTS.PANORAMIC_ACTIVATED, {});
  }

  public isEnabled() {
    return this.enabled;
  }

  public getPanoramicState = (displaySetUID) => {
    return this.panoramicStates[displaySetUID];
  }

  public addDisplaySetUID = (displaySetUID) => {
    if (!this.panoramicStates[displaySetUID]) {
      this.panoramicStates[displaySetUID] = undefined;
    }
  }

  public updateVisibility = (displaySetUID) => {
    const annotation = this.panoramicStates[displaySetUID]?.annotation;
    if (annotation) {
      cstAnnotation.visibility.setAnnotationVisibility(annotation.uid, annotation.visible);
    }
  }

  public setPanoramicState = (displaySetUID, panoramicState) => {
    this.panoramicStates[displaySetUID] = panoramicState;
    this.updateVisibility(displaySetUID);
    this._broadcastEvent(this.EVENTS.PANORAMIC_STATE_UPDATED, { displaySetUID: displaySetUID });
  }

  public setAnnotationVisible = (displaySetUID, visible) => {
    this.panoramicStates[displaySetUID].annotation.visible = visible;
    this.updateVisibility(displaySetUID);
    this._broadcastEvent(this.EVENTS.PANORAMIC_STATE_UPDATED, { displaySetUID: displaySetUID });
  }

  public setInitialImageIndex = (displaySetUID, initialImageIndex) => {
    this.panoramicStates[displaySetUID].dentascanViewportData.data[0].initialImageIndex = initialImageIndex;
    this._broadcastEvent(this.EVENTS.PANORAMIC_STATE_UPDATED, { displaySetUID: displaySetUID });
  }

  public onModeExit() {
    // TODO
  }
}
